body,
html {
  height: 842px;
  overflow: hidden;
  width: 595px;
  margin: 0;
  padding: 0;
}
.wrapper {
  margin: 0;
  padding: 50px 30px 0;
}
.wrapper .column {
  float: left;
  width: 250px;
}
.wrapper .column div.menu-section {
  margin-bottom: 0;
}
.wrapper .column div.menu-section h2.category {
  font-family: Pea Mystie;
  text-transform: uppercase;
  font-weight: 200;
  font-size: 9px;
  letter-spacing: 2.5px;
  margin-bottom: 10px;
}
.wrapper .column div.menu-section p {
  font-family: 'Avenir Next Condensed Demi';
  font-weight: lighter;
  font-style: normal;
  font-size: 10px;
  letter-spacing: 0px;
  line-height: 0.85em;
  margin: 0 0 5px 0;
}
.wrapper .column div.menu-section p span.description {
  font-family: 'Avenir Next Condensed';
  font-weight: normal;
  font-style: normal;
}
.wrapper div.left-column {
  margin-right: 65px;
}
.wrapper div.left-column img.logo {
  width: 52.5px;
  margin-bottom: 20px;
}
.wrapper div.right-column div.menu-section {
  margin-top: 5px;
}
