@font-face {
    font-family: 'Pea Mystie';
    src: url('../fonts/heyyyy-webfont.eot');
    src: url('../fonts/heyyyy-webfont.eot?#iefix') format('embedded-opentype'),
         url('../fonts/heyyyy-webfont.woff2') format('woff2'),
         url('../fonts/heyyyy-webfont.woff') format('woff'),
         url('../fonts/heyyyy-webfont.ttf') format('truetype'),
         url('../fonts/heyyyy-webfont.svg#pea_mystieregular') format('svg');
    font-weight: normal;
    font-style: normal;

}

